import logo from './logo.svg';
import React from 'react';
import './App.css';
import { Container, Row, Col } from 'react-bootstrap'
import 'bootstrap/dist/css/bootstrap.min.css';
import DvdTable from './components/DvdTable'
import DvdHeader from './components/DvdHeader'
import DvdModal from './components/DvdModal'
import DvdForm from './components/DvdForm'
import DvdCreate from './components/DvdCreate'
import DvdDetails from './components/DvdDetails'


const SERVICE_URL =  "https://tsg-dvds.herokuapp.com"

class App extends React.Component {
  state = {
    showDeleteModal: false,
    showEditModal: false,
    showAddModal: false,
    showDetails: false,
    errorMessage: false,
    dvdData: [{
                "id": 1,
                "title": "Avengers: Endgame",
                "releaseYear": 2019,
                "director": "Anthony & Joe Russo",
                "rating": "PG-13",
                "notes": "It's the Endgame!"
    
  }],
  newDvdData: {
    id: '',
    title: '',
    releaseYear: '',
    director: '',
    rating: '',
    notes: ''
  },
  editDvdData: {
    "id": 27,
    "title": "Toy Story 4",
    "releaseYear": 2019,
    "director": "John Lasster",
    "rating": "PG",
    "notes": "You've got a friend in me!"
  },
  addDvdData: {
    id: '',
    title: '',
    releaseYear: '',
    director: '',
    rating: '',
    notes: ''
  },
  viewDvdData: {
    id: '',
    title: '',
    releaseYear: '',
    director: '',
    rating: '',
    notes: ''
  },
  editDvdErrors: {
    title: '',
    releaseYear: '',
    rating: ''
  },
  addDvdErrors: {
    title: '',
    releaseYear: ''
  }
}

componentDidMount() {
  this.loadDvdData();
}


loadDvdData() {
  fetch(SERVICE_URL + "/dvds")
    .then(data => data.json())
    .then(data => this.setState(
      {dvdData: data}
    ))
}

handleDeleteModalClose = (event) => {
  this.setState({ showDeleteModal : false})
}

handleDeleteModalOpen = (event) => {
  if (event) event.preventDefault();
  let dvdId = event.target.value;
  fetch(SERVICE_URL+'/dvd/'+dvdId)
  .then(response => response.json())
  .then(data => {
      console.log('Success:', data);
      this.setState(
        { newDvdData : data , showDeleteModal : true}
      )
  })
  .catch((error) => {
      console.error('Error:', error);
  });
}
 
handleEditModalClose = (event) => {
  this.setState({ showEditModal : false})
}

handleDetailsClose = (event) => {
  this.setState({ showDetails : false})
}

handleSearch = (event) => {
  if (event) event.preventDefault();
  let category = event.target.name;
  let field = event.target.value;

  if (category == "title") {
    fetch(SERVICE_URL+'/dvds/'+'title/'+field)
    .then(response => response.json())
  .then(data => {
      this.setState(
        {dvdData : data},
      )
  })
  .catch((error) => {
      console.error('Error:', error);
  });
  }
  if (category == "releaseYear" && field >= 0) {
    fetch(SERVICE_URL+'/dvds/'+'year/'+field)
    .then(response => response.json())
  .then(data => {
      this.setState(
        {dvdData : data},
      )
  })
  .catch((error) => {
      console.error('Error:', error);
  });
  }
  if (category == "director") {
    fetch(SERVICE_URL+'/dvds/'+'director/'+field)
    .then(response => response.json())
  .then(data => {
      this.setState(
        {dvdData : data},
      )
  })
  .catch((error) => {
      console.error('Error:', error);
  });
  }
  if (category == "rating") {
    fetch(SERVICE_URL+'/dvds/'+'rating/'+field)
    .then(response => response.json())
  .then(data => {
      this.setState(
        {dvdData : data},
      )
  })
  .catch((error) => {
      console.error('Error:', error);
  });
  } else {
    if (category == "" || field == "") {
      this.setState({errorMessage: true})
    } else {
      this.setState({errorMessage: false})
    } 
  }
}

handleEditModalOpen = (event) => {
  if (event) event.preventDefault();
  let dvdId = event.target.value;
  fetch(SERVICE_URL+'/dvd/'+dvdId)
  .then(response => response.json())
  .then(data => {
      this.setState(
        {editDvdData : data , showEditModal : true},
      )
  })
  .catch((error) => {
      console.error('Error:', error);
  });
}

handleDetailsOpen = (event) => {
  if (event) event.preventDefault();
  let dvdId = event.target.value;
  
  fetch(SERVICE_URL+'/dvd/'+ dvdId)
  .then(response => response.json())
  .then(data => {
      this.setState(
        {viewDvdData : data , showDetails : true},
      )
  })
  .catch((error) => {
      console.error('Error:', error);
  });
}

  

  validateDvdAdd = (dvds) => {
    let errors = {
      title : "",
      releaseYear: "",
      rating: "",
      isValid: true
    }
  
    let isInvalid = false;
  
    if(!dvds.title){
      errors.title = "Please enter a title for the DVD."
      errors.isValid = false;
    }

    let pattern = "[0-9]{4}"
    if (dvds.releaseYear && !dvds.releaseYear.match(pattern)) {
    errors.releaseYear = "Please enter a 4-digit year."
    errors.isValid = false;
  }
  
  if(!dvds.releaseYear){
    errors.releaseYear = "Please enter a 4-digit year."
    errors.isValid = false;
  }
  
    
      if(!dvds.rating){
        errors.rating = "Please enter a rating."
        errors.isValid = false;
      }

  
      return errors;
  
  }

validateDvds = (dvds) => {
  let errors = {
    title : "",
    releaseYear: "",
    rating: "",
    isValid: true
  }

  let isInvalid = false;

  if(!dvds.title){
    errors.title = "Please enter a title for the DVD."
    errors.isValid = false;
  }


  if(!dvds.releaseYear){
    errors.releaseYear = "Please enter a 4-digit year."
    errors.isValid = false;
  }

  
    if(!dvds.rating){
      errors.rating = "Please enter a rating."
      errors.isValid = false;
    }

    if(dvds.releaseYear && (dvds.releaseYear <= 999 || dvds.releaseYear > 2021)){
      errors.releaseYear = "Please enter a 4-digit year."
      errors.isValid = false;
    }

    return errors;

}

handleEditFormChange = (event) => {

  let inputName = event.target.name;
  let inputValue = event.target.value;
  let dvdInfo = this.state.editDvdData;

  console.log(`Something changed in ${inputName} : ${inputValue}`)

  if(dvdInfo.hasOwnProperty(inputName)){
      dvdInfo[inputName] = inputValue;
      this.setState({ editDvdData : dvdInfo })
  }

}


handleAddFormChange = (event) => {

  let inputName = event.target.name;
  let inputValue = event.target.value;
  let dvdInfo = this.state.addDvdData;

  console.log(`Something changed in ${inputName} : ${inputValue}`)

  if(dvdInfo.hasOwnProperty(inputName)){
      dvdInfo[inputName] = inputValue;
      this.setState({ addDvdData : dvdInfo })
  }

}

handleAddModalClose = (event) => {
  this.setState({ showAddModal : false})
}


handleAddModalOpen = (event) => {
  this.setState({ showAddModal : true})
}

handleAddFormSubmit = (event) => {
  if (event) event.preventDefault();

  let validationErrors = this.validateDvdAdd(this.state.addDvdData)
    if(!validationErrors.isValid){
      this.setState({addDvdErrors : validationErrors})
      return
    }


  fetch(SERVICE_URL + '/dvd/', {
    method: 'POST',
    headers: {
      'Content-Type': 'application/json',
    },
    body: JSON.stringify(this.state.addDvdData),
  })
    .then(response => response.json())
    .then(data => {
      this.setState({ addDvdData: { id: '', title: '', releaseYear: '', director: '', rating: '', notes: '' }})
      this.setState({ showAddModal : false, addDvdErrors: validationErrors})
      this.loadDvdData();
    })
    .catch((error) => {
      console.log('Add DVD - Error:')
      console.log(error)
    });
}


handleEditFormSubmit = (event) => {
  if (event) event.preventDefault();
  let id = event.target.value;
  
  
  let validationErrors = this.validateDvds(this.state.editDvdData)
  if(!validationErrors.isValid){
    this.setState({editDvdErrors : validationErrors})
    return
  }


  fetch(SERVICE_URL+'/dvd/'+id, {
      method: 'PUT',
      headers: {
          'Content-Type': 'application/json',
      },
      body: JSON.stringify(this.state.editDvdData),
  })
  .then(data => {
      this.setState({ showEditModal : false, editDvdErrors : validationErrors})
      this.loadDvdData();
  })
  .catch((error) => {
      console.error('Error:', error);
  });

}

handleDeleteDVD = (event) => {
  if (event) event.preventDefault();
  let id = event.target.value;
 

  fetch(SERVICE_URL+'/dvd/'+ id, {
      method: 'DELETE',
  })
  .then(data => {
      this.setState({showDeleteModal : false})
      this.loadDvdData();
  })
  .catch((error) => {
      console.error('Error:', error);
  });
}

render() {
  const error = this.state.errorMessage;
  if (error) {
    var styl = {borderStyle: 'solid', backgroundColor: 'red'}
    var message = <h3>Both Search Category and Search Term are required.</h3>
  }
  return(
    <Container fluid>
      <Row>
          <Col>
          <DvdHeader
          handleCreate={this.handleAddModalOpen}
          handleSearch={this.handleSearch}
          />
          </Col>
          </Row>
          <hr />
      <Row>
          <Col>
          <div style ={styl}>
          {message}
          </div>
          <br></br>
          <DvdTable 
            dvd={this.state.dvdData} 
            handleDelete={this.handleDeleteModalOpen}
            handleEdit={this.handleEditModalOpen}
            handleDetails={this.handleDetailsOpen}
            />
          </Col>
          </Row>
          <DvdForm
            dvdData={this.state.editDvdData}
            show={this.state.showEditModal}
            handleClose={this.handleEditModalClose}
            handleSubmit={this.handleEditFormSubmit}
            handleChange={this.handleEditFormChange}
            dvdErrors = {this.state.editDvdErrors}
            />
          <DvdModal
          show={this.state.showDeleteModal}
          handleClose={this.handleDeleteModalClose}
          handleDelete={this.handleDeleteDVD}
          dvdData={this.state.newDvdData}
          />
          <DvdCreate
          dvdData={this.state.addDvdData}
          show={this.state.showAddModal}
          handleClose={this.handleAddModalClose}
          handleSubmit={this.handleAddFormSubmit}
          handleChange={this.handleAddFormChange}
          dvdErrors={this.state.addDvdErrors}
          />
          <DvdDetails
          dvdData={this.state.viewDvdData}
          show={this.state.showDetails}
          handleClose={this.handleDetailsClose}
          />
    </Container>
  )
}
}

export default App;
