import React from 'react';
import {Button, Modal} from 'react-bootstrap'

class DvdDetails extends React.Component {
    render() {
        let {dvdData, show, handleClose} = this.props;
        return (
            <Modal show={show} onHide={handleClose} animation={false}>
            <Modal.Dialog>
            <Modal.Header closeButton>
              <Modal.Title>{dvdData.title}</Modal.Title>
              <hr></hr>
            </Modal.Header>

            <Modal.Body>
            <p>Release Year: {dvdData.releaseYear}</p>
            <br></br>
            <p>Director: {dvdData.director}</p>
            <br></br>
            <p>Rating: {dvdData.rating}</p>
            <br></br>
            <p>Notes: {dvdData.notes}</p>
            <br></br>
            <Button variant="primary" onClick={handleClose}>Back</Button>
            </Modal.Body>
            </Modal.Dialog>
            </Modal>
        )
    }
}
export default DvdDetails