import React from 'react';
import { Form, Button, Modal } from 'react-bootstrap'

class DvdForm extends React.Component {
    render() {
        let {dvdData, handleSubmit, handleChange, show, handleClose, dvdErrors} = this.props;
        return (
            <Modal show={show} onHide={handleClose} animation={false}>
            <Modal.Dialog>
              <Modal.Header closeButton>
                <Modal.Title>Edit DVD: {dvdData.title}</Modal.Title>
                <hr></hr>
              </Modal.Header>

              <Modal.Body>
            <Form>
              <Form.Group controlId="dvdTitle">
              <Form.Label>Dvd Title:</Form.Label>
              <Form.Control type="text" placeholder="Title" name="title"
                  value={dvdData.title} onChange={handleChange}
                  isInvalid={!!dvdErrors.title}/>
                  <Form.Control.Feedback type="invalid">
                    {dvdErrors.title}
                  </Form.Control.Feedback>
             </Form.Group>
             <Form.Group controlId="dvdReleaseYear">
              <Form.Label>Release Year:</Form.Label>
              <Form.Control type="text" name="releaseYear" placeholder="Year" value={dvdData.releaseYear} onChange={handleChange}
             isInvalid={!!dvdErrors.releaseYear} pattern = "^SW[0-9]{4}$"/>
              <Form.Control.Feedback type="invalid">
                {dvdErrors.releaseYear}
              </Form.Control.Feedback>
             </Form.Group>
             <Form.Group controlId="dvdDirector">
              <Form.Label>Director:</Form.Label>
              <Form.Control type="text" placeholder="Director" name="director"
                  value={dvdData.director} onChange={handleChange}/>
             </Form.Group>
             <Form.Group controlId="dvdRating">
              <Form.Label>Rating:</Form.Label>
              <Form.Control as="select" onChange={handleChange} name="rating" isInvalid={!!dvdErrors.rating}>
                  <option value="">Please Select a Rating</option>
                  <option value="G">G</option>
                  <option value="PG">PG</option>
                 <option value="PG-13">PG-13</option>
                 <option value="R">R</option>
                  </Form.Control> 
                  <Form.Control.Feedback type="invalid">
                {dvdErrors.rating}
              </Form.Control.Feedback>  
                  </Form.Group>
            <Form.Group controlId="dvdNotes">
              <Form.Label>Notes:</Form.Label>
              <Form.Control as="textarea" rows={7} style={{width: 400}} placeholder="Notes" name="notes"
                  value={dvdData.notes} onChange={handleChange}/>
             </Form.Group>
            </Form>
            <br></br>
            <Button variant="secondary" onClick={handleClose} style ={{marginRight: 10}}>Cancel</Button>
            <Button variant="primary" onClick={handleSubmit} value={dvdData.id}>Save Changes</Button>
              </Modal.Body>
              </Modal.Dialog>
              </Modal>
        )
    }
}
export default DvdForm