import React, { Component } from 'react';
import { Table, Button } from 'react-bootstrap'

const DVDTableHeader = () => {
    return (
        <tr>
            <th>Title</th>
            <th>Release Date</th>
            <th>Director</th>
            <th>Rating</th>
            <th></th>
        </tr>
    );
}

const DVDTableRow = ({dvds, toggleEdit, toggleDelete, viewDetails}) => {
    return (
        <tr>
            <td><Button variant="link" style={{height: 35}}onClick={viewDetails} value={dvds.id}>{dvds.title}</Button></td>
            <td>{dvds.releaseYear}</td>
            <td>{dvds.director}</td>
            <td>{dvds.rating}</td>
            <td style ={{textAlign:"center", fontSize: "18px"}}><Button variant="link" onClick={toggleEdit} value={dvds.id}>Edit</Button> |  <Button variant="link" onClick={toggleDelete} value={dvds.id}>Delete</Button> </td>
        </tr>
    );
}

class DvdTable extends Component {
    static defaultProps = {
        dvd: [
            {
                "id": 1,
                "title": "Avengers: Endgame",
                "releaseYear": 2019,
                "director": "Anthony & Joe Russo",
                "rating": "PG-13",
                "notes": "It's the Endgame!"
            },
            {
                "id": 2,
                "title": "Avengers: Infinity War",
                "releaseYear": 2018,
                "director": "Anthony & Joe Russo",
                "rating": "PG-13",
                "notes": "An entire universe, once and for all."
            }
        ]
    }

    render() {
        return (
        <Table striped bordered responsive>
            <thead>
            <DVDTableHeader/>
            </thead>
            <tbody>
            {this.props.dvd.map((dvds, i) => {
                    return <DVDTableRow dvds={dvds} key={i} toggleDelete={this.props.handleDelete} toggleEdit={this.props.handleEdit} viewDetails={this.props.handleDetails}/>
                })}
            </tbody>
            </Table>
            )
    }
}
export default DvdTable;