import React from 'react';
import {Button, Modal} from 'react-bootstrap'

class DvdModal extends React.Component {
    render() {
    let {dvdData, handleDelete, show, handleClose } = this.props;
    return (
        <Modal show={show} onHide={handleClose} animation={false}>
        <Modal.Dialog>
        <Modal.Header closeButton>
          <Modal.Title>Confirmation</Modal.Title>
        </Modal.Header>

        <Modal.Body>
        <p>Are you sure you want to delete this DVD from your collection?</p>
        <br></br>
        <Button variant="secondary" onClick={handleClose} style ={{marginRight: 10}}>Cancel</Button>
        <Button variant="primary" onClick={handleDelete} value={dvdData.id}>Yes</Button>
        </Modal.Body>
        </Modal.Dialog>
        </Modal>
    )
}
}
export default DvdModal