import React, { Component } from 'react';
import {Dropdown, DropdownButton, Button } from 'react-bootstrap'

class DvdHeader extends React.Component {
    state = {
        searchCategory: "",
        searchField: ""
    }
    render() {
        let {handleCreate, handleSearch} = this.props;
        
        return (
        
            <h1>
            
          


            <Button style ={{marginRight: 700}} onClick={handleCreate}>Create DVD</Button>
            <Button style ={{marginRight: 50}} onClick={handleSearch}  name = {this.state.searchCategory} value = {this.state.searchField}>Search</Button>
            
            
            <select name='search' style={{fontSize: "20px", marginRight: 50}} onChange={(e) => this.setState({searchCategory: e.target.value})}>
                        <option value="">Search Category</option>
                        <option value="title">Title</option>
                        <option value="releaseYear">Release Year</option>
                        <option value="director">Director Name</option>
                        <option value="rating">Rating</option>
                    </select>
                  
            <input type = "text" style={{width:"320px", fontSize: "20px"}} placeholder = "Search Term" name = "term" onChange={(e) => this.setState({searchField: e.target.value})} value = {this.state.searchField}/>

            
            </h1> 
          
            
        )
    }
}
export default DvdHeader;

